.pagintaion {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap; }
  .pagintaion > * {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #262C36;
    box-sizing: border-box; }
  .pagintaion > a {
    text-decoration: none; }
    .pagintaion > a svg, .pagintaion > a img {
      max-width: 90%;
      max-height: 90%; }
    .pagintaion > a:hover {
      color: #426cff; }
    .pagintaion > a.current {
      background: #426cff;
      color: #ffffff; }

.navigation {
  display: flex;
  align-items: center;
  justify-content: center; }
  .navigation__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    padding: 0;
    background: transparent;
    border: none;
    cursor: pointer;
    color: #262C36; }
    .navigation__btn svg, .navigation__btn img {
      max-width: 90%;
      max-height: 90%; }
    .navigation__btn:hover {
      color: #426cff; }
    .navigation__btn--border {
      border: 1px solid #6b7285;
      background: #ffffff; }
      .navigation__btn--border:hover {
        background-color: #6b7285;
        color: #ffffff; }
  .navigation__dots {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 20px; }
    .navigation__dots button {
      border: none;
      padding: 0;
      cursor: pointer;
      background: #F4F4F4;
      border-radius: 50%;
      width: 10px;
      height: 10px;
      display: block;
      margin: 5px; }
      .navigation__dots button:hover:not(.active) {
        background-color: #3E5DC8; }
      .navigation__dots button.active {
        background-color: #426cff; }
    .navigation__dots--line button {
      width: 20px;
      height: 2px;
      border-radius: 0;
      margin: 5px 0; }

.zoom {
  position: relative;
  display: block; }
  .zoom > img {
    width: 100%; }
  .zoom:hover::after {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    background: rgba(0, 0, 0, 0.7) url("../images/zoom.svg") center center no-repeat;
    pointer-events: none;
    display: block; }

.accordion {
  width: 100%; }
  .accordion__title {
    padding: 21px 25px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    background-color: #F4F4F4;
    transition: background 0.3s linear; }
    .accordion__title-text {
      color: #52525A;
      transition: color 0.3s linear; }
    .accordion__title-icon {
      flex-shrink: 0;
      margin-left: 25px;
      width: 29px;
      height: 29px;
      border: 2px solid #6b7285;
      box-sizing: border-box;
      border-radius: 3px;
      position: relative;
      transition: border 0.3s linear; }
      .accordion__title-icon::after {
        content: "";
        display: block;
        width: 12px;
        height: 2px;
        background: #6b7285;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translateX(-50%) translateY(-50%);
        transition: background 0.3s linear; }
      .accordion__title-icon::before {
        content: "";
        display: block;
        width: 2px;
        height: 12px;
        background: #6b7285;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translateX(-50%) translateY(-50%);
        transition: background 0.3s linear, height 0.3s linear; }
    .accordion__title:hover .accordion__title-text {
      color: #426cff; }
    .accordion__title:hover .accordion__title-icon {
      border-color: #426cff; }
      .accordion__title:hover .accordion__title-icon::after {
        background: #426cff; }
      .accordion__title:hover .accordion__title-icon::before {
        background: #426cff; }
  .accordion__text {
    padding: 25px;
    background-color: #6b7285;
    color: #ffffff;
    display: none; }
  .accordion.active .accordion__title {
    background-color: #426cff; }
    .accordion.active .accordion__title-text {
      color: #ffffff; }
    .accordion.active .accordion__title-icon {
      border-color: #ffffff; }
      .accordion.active .accordion__title-icon::after {
        background: #ffffff; }
      .accordion.active .accordion__title-icon::before {
        background: #ffffff;
        height: 0; }

/*blocks*/
